"use client";
import Navbar from '@/components/Navbar';
import React, { useEffect, useState } from 'react';
import "@/app/css/Inicio.css";

export default function Inicio() {
  const images = [
    '/pokemones/1.jpg',
    '/pokemones/2.avif',
    '/pokemones/3.jpg',
    '/pokemones/4.jpg',
    '/pokemones/5.webp',
    '/pokemones/6.jpg',
    '/pokemones/7.jpg',
    '/pokemones/8.webp',
    '/pokemones/9.png',
  ];

  const [tiles, setTiles] = useState([]);

  useEffect(() => {
    let tempTiles = [];
    // Duplicamos varias veces para que sea más largo y continuo
    for (let i = 0; i < 5; i++) {
      images.forEach((img, index) => {
        tempTiles.push({
          img,
          height: 150 + (index % 3) * 50
        });
      });
    }
    setTiles(tempTiles);
  }, []);

  return (
    <>
      <Navbar />
      <div className="inicio-container">
        <div className="masonry-slider">
          <div className="masonry-track">
            {tiles.map((tile, index) => (
              <div className="masonry-item" key={index}>
                <img
                  src={tile.img}
                  alt={`Imagen ${index}`}
                  style={{ height: `${tile.height}px` }}
                />
              </div>
            ))}
          </div>
        </div>

        <div className="inicio-text text-white">
          <h1>Descubre lo que pokeScorpion tiene para ti!</h1>
        </div>
      </div>
    </>
  );
}
